.contacts {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 70vh;
    /*background-color: #1f1f20;*/
    flex-wrap: wrap;

}

.container {
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;

}

.contactsTitle {
    composes: containerTitle from '../../index.css';
}

.contactsForm {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 50px auto;
    width: 500px;

}

.contactsForm input {
    margin: 7px;
    height: 40px;
    background-color: #28282a;
    border: #585858 1px solid;
    font-family: 'Poppins';

}
.inputForm:focus {
    outline: 0;
    outline-offset: 0;
    border-color: #19c7a3;
    background-color: #494949;

}
.contactsForm textarea {
    height: 150px;
    background-color: #28282a;
    margin: 7px;
    border: #585858 1px solid;
    max-width: 480px;
    font-family: "Poppins";

}
.contactsForm textarea:focus {
    border-color: #19c7a3;
    outline: 0;
    outline-offset: 0;
    background-color: #494949;
}

.buttonForm {
    composes: btn from '../../index.css';
}

@media screen and (max-width: 626px) {
    .contactsForm {
        width: 100%;
    }

}