.project {
    width: 400px;
    display: flex;
    margin-bottom: 60px;
    height: max-content;
    position: relative;
}
.projectImg {
    height: 300px;
    width: 350px;
    background-size: cover;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;

}
.projectImg:hover{
    background-color: rgba(88, 88, 88, 0.5);
    transition: 0.3s;
}
.projectInfo{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    background-color: #28282a;
    width: 350px;
}
.projectButton {
    display: flex;
    flex-direction: column;
    }
.btnShow{
    composes: btn from '../../../index.css';
    background-color: #19c7a3;
    width: 170px;
    height: 35px;
}
.btnShow:hover{
    font-size: 1.3em;
    transition: 0.2s;
}

